@import "https://fonts.googleapis.com/css2?family=Bubblegum+Sans&display=swap";
body {
  background-color: #fff5f5;
  font-family: Bubblegum Sans, cursive;
}

.breed-select {
  color: #333;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  font-size: 1rem;
}

.error {
  color: red;
}

.cat-info {
  margin-top: 20px;
}

.cat-info .wrapper {
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.cat-info h2 {
  color: #ff69b4;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, .5);
  font-size: 2.5rem;
}

.cat-info .cat-description {
  color: #666;
  font-style: italic;
}

.cats-img {
  border-radius: 10px;
}

.loader {
  width: 108px;
  justify-content: space-between;
  display: flex;
  position: relative;
}

.loader:after, .loader:before {
  content: "";
  width: 48px;
  height: 48px;
  background-color: #fff;
  background-image: radial-gradient(14px, #0d161b 100%, rgba(0, 0, 0, 0) 0);
  background-repeat: no-repeat;
  border-radius: 50%;
  animation: eyeMove 10s infinite, blink 10s infinite;
  display: inline-block;
}

@keyframes eyeMove {
  0%, 10% {
    background-position: 0 0;
  }

  13%, 40% {
    background-position: -15px 0;
  }

  43%, 70% {
    background-position: 15px 0;
  }

  73%, 90% {
    background-position: 0 15px;
  }

  93%, 100% {
    background-position: 0 0;
  }
}

@keyframes blink {
  0%, 10%, 12%, 20%, 22%, 40%, 42%, 60%, 62%, 70%, 72%, 90%, 92%, 98%, 100% {
    height: 48px;
  }

  11%, 21%, 41%, 61%, 71%, 91%, 99% {
    height: 18px;
  }
}

.is-hidden {
  display: none;
}

